import * as React from 'react';
import {
  Card,
  CardBody,
  CardHeader,
  CardLink,
  CardText,
  CardTitle,
} from 'reactstrap';
import BlogModel from './Models/BlogModel';

export default class Blogs extends React.Component<{}, { blogs: BlogModel[] }> {
  constructor(props) {
    super(props);
    this.state = { blogs: Array<BlogModel>() };
  }

  async componentDidMount() {
    let response = await fetch('data/BlogData.json');
    let blogs: BlogModel[] = await response.json();
    this.setState({ blogs });
  }

  render() {
    return (
      <section id="Blogs">
        <div className="centered">
          <h2 className="mb-4">My Blogs</h2>

          <div className="blogs">
            {this.state.blogs.map((blog) => {
              return (
                <Card className="text-white bg-dark" key={blog.ID}>
                  <CardHeader>
                    <CardTitle>{blog.title}</CardTitle>
                  </CardHeader>
                  <CardBody>
                    <CardText>{blog.content}</CardText>
                    <CardLink
                      href={blog.blogURL}
                      rel="noreferrer noopener"
                      target="blank"
                    >
                      Read more...
                    </CardLink>
                  </CardBody>
                </Card>
              );
            })}
          </div>
        </div>
      </section>
    );
  }
}
